/* eslint-disable no-console */
if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.component('SensorCamera', component.exports)
}
export default {
  name: 'SensorCamera',

  props: {
    sensor: {
      type: String,
      required: true
    },
    width: {
      type: Number,
      default: 150
    },
    height: {
      type: Number,
      default: 140
    }
  },

  data() {
    return {
      updatedSensor: null,
      updatedHeight: null,
      updatedWidth: null,
      aSide: true,
      imageUrl: null,
      fpm: [1, 2, 4, 6],
      img: {},
      canvas: {},
      captures: [],
      uploadedFiles: [],
      counter: 0
    }
  },

  computed: {
    viewBox() {
      return `0 0 ${this.updatedWidth} ${this.updatedHeight}`
    },
    value: {
      get() {
        return this.updatedSensor.resources['5910']
        //  return this.normalize(this.value, this.minRangeValue, this.maxRangeValue);
      },
      set(value) {
        this.updatedSensor.resources['5910'] = value
      }
    }
  },

  watch: {
    sensor: {
      handler(sensor) {
        this.updatedSensor = JSON.parse(sensor)
      },
      immediate: true
    },
    width: {
      handler(width) {
        this.updatedWidth = width
      },
      immediate: true
    },
    height: {
      handler(height) {
        this.updatedHeight = height
      },
      immediate: true
    },
    value: {
      handler(value) {
        if (!value || value === null) return null
        if (value.type === 'Buffer') {
          console.log('camera value', Buffer.from(value.data).buffer)
          const blob = new Blob([Buffer.from(value.data).buffer])
          this.getImage(blob)
        }
      },
      immediate: true
    }
  },

  mounted() {
    this.mountElements()
    if (this.value && this.value.type === 'Buffer') {
      const blob = new Blob([Buffer.from(this.value.data).buffer])
      this.getImage(blob)
    }
  },

  beforeDestroy() {
    this.counter = 0
    this.timelapse = false
    this.elementsMounted = false
  },

  methods: {
    updateSensor(...args) {
      this.$emit('update-sensor', ...args)
    },

    deleteSensor(...args) {
      this.$emit('delete-sensor', ...args)
    },

    flipSide(value) {
      this.$emit('flip-side', value)
    },

    mountElements() {
      this.image = this.$refs[`streamViewer-${this.updatedSensor.id}`]
      this.elementsMounted = true
    },

    getImage(blob) {
      if (!this.elementsMounted) return null
      return new Promise((resolve, reject) => {
        const fReader = new FileReader()
        fReader.onload = () => {
          if (!fReader.result) reject(new Error('no result from file reader'))
          this.imageUrl = fReader.result
          resolve(this.imageUrl)
          //  resolve(this.getBase64Image(img));
        }
        if (blob instanceof Blob) {
          fReader.readAsDataURL(blob)
        }
      })
    }
  }
}
